.head-container {
  height: 70px;
  background-image: url(../img/bg_header.png); }

.logo {
  padding-left: 10px;
  font-size: 1.2em;
  color: white;
  line-height: 70px;
  font-family: "Open Sans"; }

.title {
  text-align: center;
  font-size: 1.6em;
  font-weight: bold;
  color: white;
  line-height: 70px;
  font-family: "Open Sans"; }

.status {
  text-align: right;
  font-size: 1.2em;
  color: white;
  line-height: 70px;
  font-family: "Open Sans"; }
  .status i {
    color: #fff; }

.user-link {
  padding-left: 10px;
  color: #95C857; }

body {
  background: url(../img/bg_content.png) 0 0 repeat-x transparent;
  height: 100vh; }

.btn-unify {
  color: #95C857 !important;
  margin-left: 5px !important;
  height: 25px;
  font-size: .8em !important; }
  .btn-unify:hover {
    text-decoration: underline;
    color: #fff !important; }

.btn-unify2 {
  margin-left: 8px !important;
  margin-right: 8px !important;
  color: black !important;
  background: linear-gradient(0deg, #5da407 0%, #98c63d 100%); }
  .btn-unify2:hover {
    text-decoration: underline;
    color: #fff !important; }

.nav-unify {
  text-align: center;
  top: 70px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  position: absolute;
  background: url(../img/bg_navi.png) 0 0 repeat-x transparent;
  width: 100%;
  color: #95C857;
  height: 40px; }

.footer-unify {
  height: 70px;
  bottom: 0px;
  position: fixed;
  width: 100%;
  background-color: #293037; }

.unify-text {
  font-size: 13px;
  color: #FFF; }

.main-container {
  height: auto;
  background-color: #FFF; }

.path li {
  display: inline-block;
  font-size: 1em;
  padding-left: 5px; }
  .path li a:hover {
    text-decoration: underline; }

.path label {
  font-size: 1em;
  font-weight: bold;
  color: black !important; }

.breadcrumb {
  color: black !important;
  font-size: 13px; }

.table-content {
  width: 100%; }

.line-path {
  padding-top: 0;
  padding-left: 10px;
  height: 40px;
  background-color: #FFF; }

#refresh {
  padding-right: 15px; }

table.highlight tbody tr:hover {
  background-color: #cfe9c8 !important; }

.checkbox {
  margin-left: 10px; }

[type="checkbox"].filled-in:checked [type="checkbox"].filled-in + label:after {
  border: 2px solid #5da407;
  background-color: #5da407; }

table thead {
  border-bottom: 1px solid black; }

#modal-header {
  background: url(../img/hd-bg.png);
  padding: 18px 18px 14px 18px;
  border-bottom: 1px solid #CCC;
  border-top-left-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -webkit-border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  -moz-border-radius-topright: 5px;
  -webkit-border-top-right-radius: 5px; }

.modal_close {
  position: absolute;
  top: 12px;
  right: 12px;
  display: block;
  width: 14px;
  height: 14px;
  background: url(../img/modal_close.png);
  z-index: 2; }
